.header {
  background-color: #fff;
  color: #333;

  border-bottom: 1px solid #ddd;

  padding: 0 10%;
  
  width: 100%;
  height: 80px;
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  position: sticky;
}

.header * {
  height: 100%;
}

.header h1 {
  margin: 0;
  font-size: 1.7rem;
  font-family: 'IAMAPLAYER';
}

.header ul {
  font-size: 1.1rem;
  list-style: none;
  padding: 0;
  display: flex;
}

.header li {
  width: 80px;
  margin: 0 20px;
}

.header a {
  text-decoration: none;
  color: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}

.active {
  border-bottom: 2px solid black;
}